* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  padding: 5rem 0;
  background: #5cdecc;
  background: url(/quotes_machine/static/media/b-9.6b3b5b11.jpg) no-repeat center center;
  background-size: cover;
  font-family: 'Raleway', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none
}
.App {
  width: 18rem;
  margin: 0 auto;
}

h1 {
  color: #fff;
  font-family: 'Courier';
  text-transform: capitalize;
  letter-spacing: 2px;
  font-size: 40px;
  text-align: center;
  margin-bottom: 2rem;
}

#quote-box {
  padding: 1rem;
  border-radius: 13px;
  background: #0f14177d;
  box-shadow: 0px 0px 15px 5px #ffd7008c;
}

.btn {
  height: 3rem;
  padding: 0.7rem;
  margin-bottom: 1rem;
  border: none;
  border-radius: 5px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  font-family: 'Arial';
  font-size: 120%;
  text-transform: uppercase;
  cursor: pointer;
}

.btn:active {
  -webkit-transform: translateY(5px);
  transform: translateY(5px);
}

.btn:focus {
  outline: none;
  box-shadow: 0px 0px 12px #fff;
}

#new-quote {
  background: #ffd700b5;
  color: #fff;
}

#tweet-quote {
  width: 3rem;
  float: right;
  background-color: #1DA1F2;
  color: #fff;
  text-align: center;
}

.quote {
  clear: both;
  color: #fff;
  text-shadow: 1px 1px 6px #111;
}

blockquote {
  width: 90%;
  margin: 10px auto;
  font-size: 1.4rem;
  font-weight: 600;
}

blockquote i {
  font-size: 110%;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
  margin-right: 10px;
}

cite {
  display: inline-block;
  width: 100%;
  text-align: center;
  font-size: 120%;
  font-weight: 600;
}

@media (min-width : 678px) {
  .App {
    width: 80%;
    max-width: 45rem;
    padding: 1.5rem;
  }
}
